import {instance as axios} from '../jslib/HttpRequest'

//
export function reportData(obj){
	return new Promise((resolve,reject)=>{
		axios({
			url:'/exportReport/selectBusinessDistrictAreaEmployeeStatisticsChart',
			method: 'post',
			data:obj
		})
		.then((response)=>{
            resolve(response)
		})
		.catch((error)=>{
			console.log(error)
		})
	})
}
